.about{padding:100px 20px;max-width:1200px;margin:0 auto;color:#fff;display:flex;flex-direction:column;align-items:center}.about__title{font-size:2.5rem;font-weight:bold;color:#fff;margin-bottom:40px}.about__content{display:flex;justify-content:space-between;width:100%}.about__text{flex:1;max-width:600px;margin-right:50px}.about__description{font-size:1.2rem;line-height:1.6;margin-bottom:15px}.about .highlight{text-decoration:none;color:#8e2de2;font-weight:bold}.about__hobbies-title{font-size:1.5rem;margin-top:20px;font-weight:bold}.about__hobbies{list-style-type:none;padding:0;margin-top:10px}.about__hobbies li{position:relative;font-size:1.2rem;margin-bottom:10px;padding-left:20px}.about__hobbies li::before{content:"â€¢";color:#8e2de2;position:absolute;left:0;top:0;font-size:1.5rem}.about__image{flex:1;max-width:500px;display:flex;justify-content:center;align-items:center;overflow:hidden;border-radius:12px;transition:transform .4s ease,box-shadow .4s ease}.about__image img{max-width:100%;border-radius:12px;box-shadow:0px 4px 12px rgba(0,0,0,.3);transition:transform .4s ease,box-shadow .4s ease;border:3px solid;border-image:linear-gradient(45deg, #8E2DE2, #4A00E0) 1}.about__image:hover{transform:scale(1.05) rotate(2deg);box-shadow:0px 8px 20px rgba(0,0,0,.4)}.about__image:hover img{transform:scale(1.1);box-shadow:0px 8px 20px rgba(0,0,0,.4)}@media(max-width: 768px){.about__content{flex-direction:column;align-items:center}.about__text{margin-right:0;max-width:100%;text-align:center}.about__image{width:100%;margin-top:20px;max-width:100%}.about__title{font-size:2rem}}